/**
 * cookie的读，写，移除
 */

/**
 * 通过键来获取对应的值
 * @param key
 */
export function getCookie (key) {
    let allCookies = document.cookie.split('; ')
    for (let i = 0; i < allCookies.length; i++) {
        let newAllCookies = allCookies[i].split('=')
        if (newAllCookies[0] == key){
            // 进行解码返回该值
            return decodeURI(newAllCookies[1])
        }
    }
}
