/*
*   抽取冗余的代码
* */
import {mapGetters, mapActions} from 'vuex'

export const mallMixin = {
    computed: {
        ...mapGetters([
            'userInfo',
            'userPic',
            'userId',
            'userCover',
            'userCollectonProducts',
            'userGender',
            'userDesc',
        ])
    },
    methods: {
        ...mapActions([
            'setUserInfo',
            'setUserPic',
            'setUserId',
            'setUserCover',
            'setUserCollectonProducts',
            'setUserGender',
            'setUserDesc',
        ])
    }
}
