<template>
    <header class="mooc-mall-head" :class="{'activeSearchBarFix': searchBarFixed}">
        <div class="container">
            <div class="navtop-main">
                <div class="navtop-left-logo-wrapper">
                    <router-link to="/">
                        <img src="@/assets/logo.png" alt="">
                    </router-link>
                </div>
                <div class="navtop-right-message">
                    <Button v-if="!userInfo" shape="circle" icon="ios-contact" @click="logingModal=true"></Button>

                    <Dropdown v-if="userInfo">
                        <a href="javascript:void(0)">
                            <Avatar :src="'userpic/'+userPic"/>
                        </a>
                        <DropdownMenu slot="list">
                            <DropdownItem>{{userInfo}}</DropdownItem>
                            <DropdownItem divided @click.native="changeUserPic">
                                <Icon size="20" type="ios-person-outline"/>
                                <span style="vertical-align: middle;margin-left: 5px;">我的主页</span></DropdownItem>
                            <!--<DropdownItem><router-link to="/cart">我的购物车</router-link></DropdownItem>-->
                            <DropdownItem @click.native="toCart">
                                <Icon class="mycustom" size="20" type="ios-cart-outline"/>
                                <span style="vertical-align: middle;margin-left: 5px;">我的购物车</span></DropdownItem>
                            <DropdownItem divided @click.native="logOut">
                                <Icon size="20" type="ios-exit-outline"/>
                                <span style="vertical-align: middle;margin-left: 5px;">退出登录</span></DropdownItem>
                        </DropdownMenu>
                    </Dropdown>

                    <!--<Avatar v-if="userInfo" src="https://i.loli.net/2017/08/21/599a521472424.jpg" />-->
                    <Modal v-model="logingModal" width="350">
                        <p slot="header" style="text-align:center">
                            <span>请登录</span>
                        </p>
                        <div class="modal-content">
                            <div class="username">
                                <Input type="text" clearable v-model="username" prefix="ios-contact"
                                       placeholder="输入用户名"
                                       style="width: 100%"/>
                            </div>
                            <div class="password">
                                <Input type="password" clearable @keyup.enter.native="login" v-model="password"
                                       prefix="ios-lock" placeholder="输入密码" style="width: 100%"/>
                            </div>
                            <div class="remberPass">
                                <Checkbox v-model="remberPassBtn">自动登录</Checkbox>
                            </div>
                        </div>
                        <div slot="footer">
                            <Button v-if="!modal_loading" type="primary" size="large" long :loading="modal_loading"
                                    @click="login">登录
                            </Button>
                            <Button v-if="modal_loading" type="primary" size="large" long :loading="modal_loading">
                                登录中
                            </Button>
                        </div>
                    </Modal>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
    import axios from 'axios'
    import { getCookie } from '../utils/cookies'
    import { mallMixin } from '../utils/mixin'

    export default {
        name: 'HelloWorld',
        mixins: [mallMixin],
        inject: ['reload'],
        data () {
            return {
                logingModal: false,
                modal_loading: false,
                username: '',
                password: '',
                remberPassBtn: true,
                searchBarFixed: false,
            }
        },
        mounted () {
            this.checkLogin()
            this.initUsername()
            this.initScollEvent()
        },
        methods: {
            // 去到购物车页面
            toCart () {
                this.$router.push('/cart')
            },
            // 初始化顶部logo动画以及位置
            initScollEvent () {
                window.addEventListener('scroll', this.initScollAnimation)
            },
            initScollAnimation () {
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                let offsetTop = null
                if (document.getElementById('GoodsOffsetTop')) {
                    offsetTop = document.getElementById('GoodsOffsetTop').offsetTop
                }
                //let offsetTop = document.querySelector('#GoodsOffsetTop').offsetTop || null
                // console.log(parseInt(scrollTop))
                if (offsetTop != null) {
                    scrollTop > offsetTop ? this.searchBarFixed = true : this.searchBarFixed = false
                }
            },
            initUsername () {
                this.username = getCookie('username')
            },
            login () {
                if (!this.username || !this.password) {
                    this.$Message.error('用户名或密码不能为空')
                    return
                    // 用户名正则，4到16位（字母，数字，下划线，减号）
                    // 密码强度正则，最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符
                } /*else if (!this.username.match( /^[a-zA-Z0-9_-]{4,16}$/) || !this.password.match(/^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/)) {
                    this.$Message.error('用户名或密码格式不正确')
                    return
                }*/
                this.modal_loading = true
                axios.post('/users/login', {
                    username: this.username,
                    password: this.password,
                    autoLoginBtn: this.remberPassBtn
                }).then(res => {
                    // console.log(res.data)
                    // 系统错误
                    if (res.data.status == '1') {
                        this.$Message.error('系统未知错误！')
                        this.modal_loading = false
                        this.logingModal = false
                        // 用户名或密码错误
                    } else if (res.data.status == '2') {
                        this.$Message.error('用户名或密码错误')
                        this.modal_loading = false
                        // 正确
                    } else if (res.data.status == '0') {
                        this.modal_loading = false
                        this.logingModal = false
                        // 注入vuex中username的值
                        this.setUserInfo(res.data.result.username)
                        this.setUserPic(res.data.result.userPic)
                        this.setUserId(res.data.result.userId)
                        this.setUserCover(res.data.result.userCover)
                        this.setUserCollectonProducts(res.data.result.userCollectonProducts)
                        this.setUserGender(res.data.result.userGender)
                        this.setUserDesc(res.data.result.userDesc)

                        this.$Message.success('登陆成功')
                        this.$nextTick(() => {
                            this.reload()
                        })
                    }
                })
                // setTimeout(() => {
                //     this.modal_loading = false;
                //     this.logingModal = false;
                //     this.$Message.error('登陆成功');
                // }, 2000);
            },

            // 更换用户头像
            changeUserPic (userId) {
                if (userId) {
                    this.$router.push({
                        path: `/changeUserPic`
                    })
                }
            },

            // 注销
            logOut () {
                axios.post('/users/logOut').then(res => {
                    if (res.data.status == '0') {
                        this.setUserInfo(res.data.result.username)
                        //this.username = getCookie('username')
                        this.$Message.info('注销成功')
                        // this.$router.replace({
                        //     path: '/empty',
                        //     name: 'empty'
                        // })
                        // this.reload()
                        // this.$emit('updateGoods')
                    }
                })
            },

            // 自动登录
            checkLogin () {
                axios.get('/users/checkLogin').then(res => {
                    // 验证成功
                    if (res.data.status == '0') {
                        // 注入vuex中username的值
                        this.setUserInfo(res.data.result.username)
                        this.setUserPic(res.data.result.userPic)
                        this.setUserId(res.data.result.userId)
                        this.setUserCover(res.data.result.userCover)
                        this.setUserCollectonProducts(res.data.result.userCollectonProducts)
                        this.setUserGender(res.data.result.userGender)
                        this.setUserDesc(res.data.result.userDesc)
                    } else {
                        // 强行定位到主页
                        if (this.$route.path != '/') {
                            this.$router.replace('/')
                        }
                    }
                })
            },

        },
        destroyed () {
            window.removeEventListener('scroll', this.initScollAnimation)
        }
    }
</script>

